export default {
  title: 'Modality',
  content: [
    [
      {
        type: 'title',
        value: 'Modality'
      },
      {
        type: 'sub',
        value: 'AppletConfig > modality'
      },
      {
        type: 'text',
        value: 'Whether the Applet is displayed in the form of a modal box.'
      },
      {
        type: 'hr'
      },
      {
        type: 'text',
        value: '<h3>Types</h3>'
      },
      {
        type: 'code',
        value: `
          <code-highlight type="ts">
            type ModalityType = 'sheets:bottom'
            interface AppletBaseConfig {
              modality?: ModalityType
            }
          </code-highlight>
        `
      },
      {
        type: 'hr'
      },
      {
        type: 'text',
        value: '<h3>Details</h3>'
      },
      {
        type: 'text',
        value: 'Currently only the <b>sheets:bottom</b> type of modal effect is supported, which appears as an overlay window that pops up from the bottom.'
      },
      {
        type: 'text',
        value: 'The <b>sheets:bottom</b> type also provides a custom option <b>SheetsBottomOptions</b>'
      },
      {
        type: 'code',
        value: `
          <code-highlight type="ts">
            type ModalityType = 'sheets:bottom'
            type SheetsBottomOptions = {
              top?: string,
              maskOpacity?: number
              blockedHolderWidth?: number | 'string'
              alwaysPopUp?: boolean
              maskClosable?: boolean
            }
            interface AppletBaseConfig {
              modality?: ModalityType
              sheetsBottomOptions?: SheetsBottomOptions
            }
          </code-highlight>
        `
      },
      {
        type: 'text',
        value: `
          <ul>
            <li>
              <b>top</b>: The edge distance from the top of the view to the top of the window, CSS units are supported.
            </li>
            <li>
              <b>maskOpacity</b>: The opacity of the background black mash, default is 0.3.
            </li>
            <li>
              <b>blockedHolderWidth</b>: The safe margin of the left finger edge sliding (the size of the margin will cover the interactive area), and the boundary value determines the sensitivity of the left sliding closing.
            </li>
            <li>
              <b>alwaysPopUp</b>: Whether to always keep the popup animation when the window is switched, the default is off, and the window remains still when the scene is switched.
            </li>
            <li>
              <b>maskClosable</b>: Click whether the background mask is allowed to close the window, the default is closed.
            </li>
          </ul>
        `
      },
      {
        type: 'hr'
      },
      {
        type: 'text',
        value: '<h3>Example</h3>'
      },
      {
        type: 'code',
        value: `
          <code-highlight type="ts">
            createApplication({
              applets: {
                pageA: {
                  config: {
                    modality: 'sheets:bottom'
                    sheetsBottomOptions: {
                      top: '10%',
                      maskOpacity: 0.5,
                      blockedHolderWidth: 20,
                      alwaysPopUp: false,
                      maskClosable: true
                    }
                  }
                }
              }
            })
          </code-highlight>
        `
      },
      {
        type: 'hr'
      },
      {
        type: 'text',
        value: '<h3>Notes</h3>'
      },
      {
        type: 'text',
        value: 'Note that the sheetsBottomOptions option is a further feature description when the modality option is \'sheets:bottom\', which needs to be used in conjunction and is not valid when used alone.'
      }
    ]
  ]
}